import React, { FC } from 'react';
// import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';


import { IModalComponent } from './IModalComponent';

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
        justifyContent:'center'
	},
	dialogContentContainer: {
        width: '100%'
	},
	container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '450px'
	},
	closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
	},
	dialogTitle: {
        marginTop: theme.spacing(2),
        // marginBottom: theme.spacing(2),
        fontSize: '16px',
        color: '#272E4C',
		fontWeight: 700
    },
}));

export const ModalComponent: FC<IModalComponent.IProps> = props => {
	const classes = useStyles();

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={props.onClose}
			aria-labelledby="form-dialog-title"
			maxWidth="sm"
			className={classes.dialog}
		> 
			 <div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
						<DialogTitle className={classes.dialogTitle} disableTypography={false} id="form-dialog-title" >
							<Box fontWeight={700}>{props.title}</Box>
							<IconButton aria-label="close" className={classes.closeButton} onClick={props.onClose}>
                                <CloseIcon />
                            </IconButton>
						</DialogTitle>
					</Grid>
					<Grid item xs className={classes.dialogContentContainer}>
						{
						props.message ? 
							<DialogContent>
								<Grid container spacing={2} justify="space-between" alignItems="flex-start">
									<Grid item xs={12}>
										{props.message}
									</Grid>
								</Grid>
							</DialogContent> : null
						} 
						{
							props.children ? props.children : null
						}
						<DialogActions className={classes.dialogAction}>
							{ props.buttons !== undefined ?
								props.buttons: null
							}
						</DialogActions>
					</Grid>
                </Grid>
			</div>
		</Dialog>
	);
};

ModalComponent.defaultProps = {
	isModalOpen: false,
};
