import * as React from 'react';
import {
    Grid,
    InputLabel,
    Typography,
    Checkbox
} from '@material-ui/core';
import moment from 'moment';
import { BasicTextField } from 'Components/UI';
import { KeyboardDatePicker } from '@material-ui/pickers';

interface IEditPoliciesProps {
    classes?: any,
    rows: any,
    dataChanges: (data) => void,
}

export const EditPolicies: React.FC<IEditPoliciesProps> = (props: IEditPoliciesProps): JSX.Element => {
    const {
        rows,
        classes,
        dataChanges
    } = props;

    const [editData, setEditData] = React.useState<any>({
        description: null,
        isProcessMaker: null,
        effectivityDate: '',
        expiryDate: '',
    })


    return (
        <>
            <Grid item xs={4}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">MODULE</InputLabel>
                <Typography><b>{rows.module}</b></Typography>
                {/* <Link onClick={() => { setIsModuleModalOpen(true) }}>Change Module</Link> */}
            </Grid>
            <Grid item xs={4}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE ID</InputLabel>
                <Typography><b>{rows.role_id}</b></Typography>
            </Grid>
            <Grid item xs={4}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">DESCRIPTION</InputLabel>
                <BasicTextField
                    value={editData.description !== null ? editData.description : rows.description}
                    onChange={(e: any) => {
                        setEditData({
                            ...editData,
                            description: e.target.value
                        })
                    }}
                    onBlur={(e: any) => {
                        dataChanges({
                            ...editData,
                            description: e.target.value
                        })
                    }}
                    disabled={rows.is_processMaker ? true : false}
                ></BasicTextField>
            </Grid>
            <Grid item xs={4}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">ROLE NAME</InputLabel>
                <Typography><b>{rows && rows.name ? rows.name : '-'}</b></Typography>
            </Grid>
            <Grid item xs={4}>
                <InputLabel className={classes.inputLabel}>IS A PROCESSMAKER PROCESS MAKER ROLE</InputLabel>
                <Checkbox
                    checked={editData.isProcessMaker !== null ? editData.isProcessMaker : rows.is_processMaker}
                    onChange={(e: any) => {
                        setEditData({
                            ...editData,
                            isProcessMaker: e.target.checked
                        })
                        dataChanges({
                            ...editData,
                            isProcessMaker: e.target.checked
                        })
                    }}
                    disabled={true}
                ></Checkbox>
            </Grid>
            <Grid item xs={4}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">STATUS</InputLabel>
                <Typography><b>{rows && rows.status}</b></Typography>
            </Grid>
            <Grid item xs={6}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">EFFECTIVITY DATE</InputLabel>
                <KeyboardDatePicker
                    maxDate={moment(editData.expiryDate).subtract(1, 'days').startOf('day')}
                    maxDateMessage={"Effectivity date cannot be higher or equal than expiry date."}
                    value={editData.effectivityDate !== '' ? moment(editData.effectivityDate) : moment(rows.effectivity_date)}
                    format={'MMM DD, YYYY'}
                    style={{ width: "100%", marginTop: 10 }}
                    onChange={(e: any) => {
                        setEditData({
                            ...editData,
                            effectivityDate: e
                        })
                        dataChanges({
                            ...editData,
                            effectivityDate: e
                        })
                    }}
                    disabled={rows.is_processMaker ? true : false}
                    inputVariant={'outlined'}
                />
            </Grid>
            <Grid item xs={6}>
                <InputLabel className={classes.inputLabel} htmlFor="role_id">EXPIRY DATE (If applicable)</InputLabel>
                <KeyboardDatePicker
                    minDate={moment(editData.effectivityDate).add(1, 'days').startOf('day')}
                    minDateMessage={"Expiry date cannot be lower or equal than effectivity date."}
                    value={editData.expiryDate !== '' ? moment(editData.expiryDate) : rows.expiry_date ? moment(rows.expiry_date) : null}
                    style={{ width: "100%", marginTop: 10 }}
                    invalidDateMessage=""
                    format="MMMM DD, YYYY"
                    onChange={(e: any) => {
                        setEditData({
                            ...editData,
                            expiryDate: e,
                        })
                        dataChanges({
                            ...editData,
                            expiryDate: e,
                        })
                    }}
                    disabled={rows.is_processMaker ? true : false}
                    inputVariant={'outlined'}
                />
            </Grid>


        </>
    )
}
