export const RolesPage: React.FC<IRolesPage.IProps> = (props: IRolesPage.IProps): JSX.Element => {
	const classes = useStyles();
	const [viewOnly, setViewOnly] = useState(true);
	const [isEdited, setIsEdited] = useState(false);
	const [loading, setLoading] = useState(false);
	const [activeItem, setActiveItem] = useState('Policies');
	const [activeItemIndex, setActiveItemIndex] = useState(0);
	const [isModalConfirmExitOpen, setIsModalConfirmExitOpen] = useState(false);
	const [isModalConfirmSaveOpen, setIsModalConfirmSaveOpen] = useState(false);
	const [isModalSaveSuccessOpen, setIsmodalSaveSuccessOpen] = useState(false);
	const [mode, setMode] = useState('view');
	// const [dataResponse, setDataResponse ] = useState([]);
	const [isOpenSideModalBlock, setIsOpenSideModalBlock] = useState(false);
	const [policyResponse, setPolicyResponse] = useState<any>([]);
	const [moduleName, setModuleName] = useState('User Management');
	const [rbacList, setRbacList] = useState([]);
	const [breadCrumbs, setBreadCrumbs] = useState<any[]>([])
	const [rbacCount,] = useState(0);
	const [roleId, setRoleId] = useState('');
	const [policyData, setPolicyData] = useState<any>({})
	const [isRedirect, setIsRedirect] = useState<boolean>(false)
	const [rbacState, setRbacState] = useState<any>({
		filter: [],
		order: '',
		limit: 40,
		skip: 0,
		count: 0,
		currentPage: 0
	})
	const [typingTimeout, setTypingTimeout] = useState<any>(0);
	const [showDeactivationWarning, setShowDeactivationWarning] = useState<boolean>(false)

	const sideBarItems = [
		{
			name: 'Policies',
			// label: 'POLICY'
		},
		// {
		// 	name: 'Groups And Users',
		// 	// label: 'GROUPIE'
		// },
	];

	React.useEffect(() => {
		handleGetRoleData()
	}, []);

	const handleGetRoleData = () => {
		setMode('view');
		setLoading(true)
		getRoleData(props.match.params.id).then(response => {
			var _crumbs: any[] = [
				{
					name: "ROLES",
					link: "/system-admin/users/roles"
				},
			];
			_crumbs.push({ name: response.name })
			setBreadCrumbs(_crumbs);
			setPolicyResponse(response);
			handGetRbacList(
				rbacState.filter,
				rbacState.order,
				rbacState.limit,
				rbacState.skip,
				rbacState.currentPage,
				response,
				'view',
			);
			console.log(response)
		}).catch(err => {
			setLoading(false)
			console.log(err)
			alert(err);
		})
	}

	const handGetRbacList = (
		filter: any,
		sort: any,
		limit: number,
		skip: number,
		currentPage: number,
		resParam?: any,
		_mode?: string,
		search?: string,
	) => {

		if (typingTimeout) {
			clearTimeout(typingTimeout)
		}

		let _filter: any = {};
		let _order = sort ? `${sort[0].columnName} ${sort[0].direction}` : '';

		if (filter.length > 0) {
			filter.map((item: any) => {
				_filter[item.columnName] = {
					like: `^${item.value}.*`,
					options: "i"
				}
			})
		} else {
			_filter = {
				module: {
					like: "^.*",
					options: "i"
				}
			}
		}

		let params = {
			limit: limit,
			skip: skip,
			order: _order,
			where: _filter
		}

		setTypingTimeout(setTimeout(() => {
			setLoading(true)
			getRbacList(params, search ? search : '').then(response => {
				let res = response.body;
				setRbacState({
					currentPage: currentPage,
					filter: filter,
					order: sort,
					limit: limit,
					skip: skip,
					count: response.count
				})

				res.length > 0 && res.map((item: any) => {
					resParam.rbac && resParam.rbac.length > 0 && resParam.rbac.map((policy: any) => {
						if (item._id === policy._id) {
							item['checked'] = true;
						}
					})
				})

				// if (_mode === 'view' && resParam) {
				// 	res = res.length > 0 && res.filter((item: any) => {
				// 		return item.checked ? true : false
				// 	})
				// }

				setRbacList(res)
				setLoading(false)
			}).catch(err => {
				console.log(err)
				setLoading(false)
				alert(err);
			})
		}, filter.length > 0 || search ? 2000 : 0))
	}

	const editRole = () => {
		setViewOnly(false);
		setMode('edit');
		handGetRbacList(
			rbacState.filter,
			rbacState.order,
			rbacState.limit,
			rbacState.skip,
			rbacState.currentPage,
			policyResponse,
			'edit',
		)
	}

	const activeSideBarItem = (item) => {
		console.log(item)
		setActiveItem(item);
		setViewOnly(true);
		sideBarItems.map((data, index) => {
			if (data.name === item) {
				if (item === 'Groups And Users') {
					setIsOpenSideModalBlock(true)
				}
				setActiveItemIndex(index);
			}
		})
	}

	const handleModalConfirmationClose = () => {
		setIsModalConfirmSaveOpen(false);
		setIsModalConfirmExitOpen(false);
	}

	const generateRoleId = (moduleName: string, count: number) => {
		let roleId = '';

		switch (moduleName) {
			case 'User Management':
				roleId = `UMRID0${count}`
				break;
			case 'Membership':
				roleId = `MRID0${count}`
				break;
			case 'Underwriting':
				roleId = `URID0${count}`
				break;
			case 'Customer Care':
				roleId = `CRID0${count}`
				break;
			case 'Claims':
				roleId = `CLRID0${count}`
				break;
			default:
				roleId = `UMRID0${count}`
				break;
		}
		return roleId;
	}

	const handleChangeModuleName = (moduleName) => {
		setRoleId(generateRoleId(moduleName, rbacCount));
	}


	const handleOnSave = () => {
		let data = policyData;

		if (!data["expiry_date"] && data.expiry_date === null) {
			delete data.expiry_date
		}

		data.rbac && data.rbac.map((rbac: any) => {
			if (rbac.checked) {
				delete rbac.checked
			}
		})
		setLoading(true)
		updateRoleRbac(data._id, data).then((response: any) => {
			console.log(response)
			setMode('view');
			handGetRbacList(
				rbacState.filter,
				rbacState.order,
				rbacState.limit,
				rbacState.skip,
				rbacState.currentPage,
				response,
				'view',
			)
			setPolicyResponse(response)
			setIsmodalSaveSuccessOpen(true)
			setIsModalConfirmSaveOpen(false);
			setBreadCrumbs([
				{
					name: "ROLES",
					link: "/system-admin/users/roles"
				},
				{
					name: response.name
				}
			])
			setModuleName(response.module)
			setLoading(false);
			setIsEdited(false);
		}).catch(err => {
			setLoading(false)
			alert(err);
		})
	}

	const handleRemoveAllChanges = () => {
		handleGetRoleData()
		handGetRbacList(
			rbacState.filter,
			rbacState.order,
			rbacState.limit,
			rbacState.skip,
			rbacState.currentPage,
			policyResponse,
			'view',
		)
		setIsModalConfirmExitOpen(false)
		setIsRedirect(true)
	}

	const renderSaveChangesMessage = () => {
		if (showDeactivationWarning)
			return (
				<>
					<span>Save all changes made?</span>
					<br />
					<br />
					<strong>NOTE:</strong><br />
					<span>You changed the expiry date of this role. This will deactivate the role.</span>
				</>
			)
		else
			return 'Save all changes made?'
	}

	return (
		<>
			{
				isRedirect &&
				<Redirect
					to={{
						pathname: '/system-admin/users',
					}}
				/>
			}
			<SubHeader
				headingTitle={breadCrumbs[1] ? breadCrumbs[1].name : ''}
				breadCrumbsArray={breadCrumbs}
				buttonFunc={editRole}
				buttonName={'Edit Role'}
				layoutType={'layout_1'}
				enableButton={mode === 'view' && activeItem === 'Policies' && policyResponse.is_processmaker === false ? true : false}
			></SubHeader>
			<div className={classes.main}>
				{loading ?
					(<LoadingIndicator />) : null
				}
				<Grid container xs={12}>
					<Grid item xs={3} className={classes.sideBarContainer}>
						<SideMenu
							items={sideBarItems}
							activeItems={activeSideBarItem}
							setActiveItem={activeItemIndex}
						></SideMenu>
					</Grid>
					<Grid item xs={9} className={classes.contentContainer}>
						{
							activeItem == 'Policies' &&
							<Policies
								rbacList={rbacList}
								moduleName={moduleName}
								roleId={roleId}
								rows={policyResponse}
								mode={mode}
								classes={classes}
								viewOnly={viewOnly}
								isEdited={isEdited}
								rbacState={rbacState}
								onSearch={(search: string) => {
									handGetRbacList(
										rbacState.filter,
										rbacState.order,
										rbacState.limit,
										0,
										rbacState.page,
										policyData ? policyData : policyResponse,
										mode,
										search
									)
								}}
								onNavigateSideMenu={(name: string) => {
									activeSideBarItem(name)
								}}
								onChangeModuleName={(name: string) => {
									handleChangeModuleName(name)
								}}
								onUpdateChange={(data: any) => {
									setPolicyData(data);
									setIsEdited(true);
								}}
								onPageChange={(page: number) => {
									let skip = page * rbacState.limit
									handGetRbacList(
										rbacState.filter,
										rbacState.order,
										rbacState.limit,
										skip,
										page,
										policyData ? policyData : policyResponse,
										mode,
									)

								}}
								onPageSizeChange={(size: number) => {
									handGetRbacList(
										rbacState.filter,
										rbacState.order,
										size,
										rbacState.skip,
										rbacState.currentPage,
										policyData ? policyData : policyResponse,
										mode,
									)
								}}
								onFilterChange={(filter: any) => {
									handGetRbacList(
										filter,
										rbacState.order,
										rbacState.limit,
										0,
										0,
										policyData ? policyData : policyResponse,
										mode,
									)
								}}
								onSortChange={(sort: any) => {
									handGetRbacList(
										rbacState.filter,
										sort,
										rbacState.limit,
										rbacState.skip,
										rbacState.currentPage,
										policyData ? policyData : policyResponse,
										mode,
									)
								}}
							/>
						}
						{
							activeItem == 'Groups And Users' &&
							<GroupsAndUsers
								rows={policyResponse}
								classes={classes}
								viewOnly={viewOnly}
								openSideModalBlock={isOpenSideModalBlock}
							></GroupsAndUsers>
						}
					</Grid>
				</Grid>
			</div>
			<ModalComponent
				isModalOpen={isModalConfirmSaveOpen}
				onClose={handleModalConfirmationClose}
				message={renderSaveChangesMessage()}
				title={'Confirmation'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button onClick={() => { setIsModalConfirmSaveOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
						</Grid>
						<Grid item xs={6}>
							<Button
								className={classes.button}
								variant={'contained'}
								color={'primary'}
								onClick={() => handleOnSave()}
							>Save</Button>
						</Grid>
					</Grid>
				}
			/>
			<ModalComponent
				isModalOpen={isModalConfirmExitOpen}
				onClose={handleModalConfirmationClose}
				message={'Exit and remove all changes made?'}
				title={'Confirmation'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button
								onClick={() => {
									setIsModalConfirmExitOpen(false)
								}}
								className={classes.button}
								variant={'outlined'}
								color={'secondary'}
							>Cancel</Button>
						</Grid>
						<Grid item xs={6}>
							<Button
								className={classes.button}
								variant={'contained'}
								color={'primary'}
								onClick={() => {
									handleRemoveAllChanges()
								}}
							>Continue</Button>
						</Grid>
					</Grid>
				}
			/>
			<ModalComponent
				isModalOpen={isModalSaveSuccessOpen}
				onClose={() => { setIsmodalSaveSuccessOpen(false) }}
				message={'Changes has been saved.'}
				title={'Save Success'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button
								className={classes.button}
								variant={'contained'}
								color={'primary'}
								onClick={() => { setIsmodalSaveSuccessOpen(false) }}
							>Okay</Button>
						</Grid>
					</Grid>
				}
			/>
			{
				mode === 'edit' && activeItem === 'Policies' && policyData !== '' && isEdited === true ?
					<FloatingFooter>
						<Grid container spacing={1} justify={'flex-end'} style={{ textAlign: 'right' }}>
							<Grid item>
								<Button
									className={classes.button}
									variant={'contained'}

									color={'primary'}
									style={{ backgroundColor: '#3C394A' }}
									onClick={() => setIsModalConfirmExitOpen(true)}
								>Cancel</Button>
							</Grid>
							<Grid item>
								<Button
									className={classes.button}
									variant={'contained'}
									color={'primary'}
									onClick={() => {
										const today = moment().startOf('day')
										const exp_date = policyData && policyData.expiry_date ? moment(policyData.expiry_date).startOf('day')  : undefined;
										
										if (today.isSameOrAfter(exp_date)) {
											setShowDeactivationWarning(true)
										} else {
											setShowDeactivationWarning(false)
										}

										setIsModalConfirmSaveOpen(true)
									}}
								>Save Changes</Button>
							</Grid>
						</Grid>
					</FloatingFooter>

					: ""
			}
		</>
	);
}

const mapStateToProps = (state: Store) => state.roles;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(RolesActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
