import * as React from 'react';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons';
// import { InputLabel } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid } from '@material-ui/core';
import { getAllGroups } from 'Pages/API/Calls/Roles';

const useStyles = makeStyles(() => ({
    selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px'
    },
    xIcon: {
        fontSize: 16
    }
}));
console.log(useStyles);

interface IAddGroupRoleBodyProps {
    onClose: () => void;
    onSave: (items) => void;
}

interface IAddGroupRoleBodyState {
    selectedItems: any;
    comboArray: any[];
}


export class AddGroupRoleBody extends React.Component<IAddGroupRoleBodyProps, IAddGroupRoleBodyState> {
    constructor(props) {
        super(props);
        this.state = {
            selectedItems: [],
            comboArray: [],
        }
    }

    componentDidMount() {
        getAllGroups().then(res => {
            let data = {
                body: res.body,
                label: "name",
            }
            let newChoices: any = [];

            if (data.body && data.body.length > 0) {
                data.body.map(c => {
                    let beauty = {
                        name: c[data.label],
                        value: c
                    }
                    newChoices.push(beauty)
                })
                this.setState({
                    comboArray: newChoices
                })
                console.log(this.state.comboArray)
            }
        }).catch(err => {
            console.log(err);
        })
    }


    handleOnSearch = (list) => {
        let arr = this.state.selectedItems;
        arr.push(list);
        this.setState({
            selectedItems: arr
        })
        console.log(arr);
    }

    handleRemoveItem = (index) => {
        let arr = this.state.selectedItems;
        arr.splice(index, 1);
        this.setState({
            selectedItems: arr
        })
    }

    handleOnSaveItems = () => {
        this.props.onSave(this.state.selectedItems);
    }

    public render(): JSX.Element {
        const classes = {
            buttonsContainer: {
                padding: '1em',
            },
            selectedContainer: {
                padding: '3em 0 2em'
            },
            selectedItems: {
                borderRadius: 20,
                fontSize: 13,
                backgroundColor: '#d1d4db',
                padding: '5px 10px',
                margin: '5px',
                display: 'inline-block',
            },
            selectedText: {
                paddingRight: '5px'
            },
            saveButton: {
                width: '135px',
                fontSize: '14px',
                color: '#fff',
            },
            cancelButton: {
                width: '135px',
                fontSize: '14px',
            }
        };

        return (
            <>
                <ReactSearch
                    searchLabel={'Search user group'}
                    onSearch={this.handleOnSearch}
                    comboBoxMode={true}
                    comboListsArray={this.state.comboArray}
                />
                <div style={classes.selectedContainer}>
                    <div>
                        <label>Selected Groups</label>
                    </div>
                    {this.state.selectedItems.map((item, index) =>
                        <div style={classes.selectedItems}>
                            <span style={classes.selectedText}>{item}</span>
                            <span onClick={() => this.handleRemoveItem(index)}>
                                <FontAwesomeIcon icon={faTimesCircle} />
                            </span>
                        </div>
                    )
                    }
                </div>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.props.onClose()}>Cancel</Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button disabled={this.state.selectedItems.length < 1 ? true : false} style={classes.saveButton} variant="contained" color="primary" onClick={() => this.handleOnSaveItems()}>Save</Button>
                    </Grid>
                </Grid>
            </>
        )
    }

}
